.App {
  text-align: center;
  font-family: Satoshi;
}
:root {
  background-color: rgb(0, 0, 0);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.keydown {
  opacity: 0.3 !important;
}

#disclaimer {
  font-size: 10px;
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

}

.key {
  width: 50px;
  height: 50px;
  padding: 10px;
}

.bold {
  font-weight: bold;
}

.key-container {
  opacity: 0.5;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 30px;
}

#nav-buttons {
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#opaque {
  opacity: 0.5;
}

.App-link {
  color: #61dafb;
}

@keyframes slide-scale-up {
  from {
    opacity: 0;
    transform: scale(3);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes reverse-anim {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@font-face {
  font-family: Satoshi;
  src: url(assets/Satoshi-Medium.otf);
}
@font-face {
  font-family: Satoshi-Bold;
  src: url(assets/Satoshi-Bold.otf);
}

::view-transition-new(root) {
  animation-name: slide-scale-up;
  animation-duration: 250ms;
}


.back-animation::view-transition-new(root) {
  animation-name: reverse-anim;
  animation-duration: 250ms;
}

#point-list {

  text-align: left;
}

a {
  color: #69deff;
}
a:visited {
  color: rgb(220, 133, 255);
}

#bordered-div {
  border-style: solid;
  border-radius: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 30px;
  padding-right: 30px;
}

.point-header {
  border-style: solid;
  border-radius: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 30px;
  padding-right: 30px;
  text-align: center !important;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
